export default class FrameView {
  constructor(frame) {
    this.canva = frame || null;
  }

  renderFrame() {
    const frameList = document.querySelector('.frames-list');
    const frameItem = document.createElement('div');
    frameItem.classList.add('frames-item');
    frameItem.classList.add('current');
    frameItem.innerHTML = `
      <canvas height="100px" width="100px"></canvas>
      <button class="copy_frame">+</button>
      <button class="delete_frame">x</button>
    `;
    frameList.insertBefore(frameItem, frameList.lastElementChild);
    if (this.canva) {
      frameItem.querySelector('canvas').getContext('2d').drawImage(this.canva, 0, 0);
    } else {
      this.canva = frameItem.querySelector('canvas');
    }
  }
}
