import AppView from '../view/appview';

export default class AppController {
  constructor() {
    this.frames = 0;
  }

  start() {
    AppView.renderInitialPage();
    const speed = document.querySelector('input[type="range"]');
    const fullScreen = document.querySelector('.full_screen');
    this.frames += 1;
    const frameList = document.querySelector('.frames-list');
    const centerCanvas = document.querySelector('.center_canvas');

    frameList.addEventListener('click', (event) => {
      let { target } = event;
      while (target !== this) {
        if (target.classList.contains('add_frame')) {
          event.preventDefault();
          AppView.addNewFrame();
          AppView.animateFrame(speed.value);
          return;
        }
        if (target.classList.contains('delete_frame')) {
          event.preventDefault();
          AppView.deleteFrame(target.parentElement);
          AppView.animateFrame(speed.value);
          return;
        }
        if (target.classList.contains('copy_frame')) {
          event.preventDefault();
          AppView.copyFrame(target.parentElement);
          AppView.animateFrame(speed.value);
          return;
        }
        if (target.classList.contains('frames-item')) {
          event.preventDefault();
          AppView.changeCurrentFrame(target);
          return;
        }
        target = target.parentNode;
      }
    });

    speed.addEventListener('change', (e) => {
      AppView.animateFrame(e.target.value);
    });

    fullScreen.addEventListener('click', () => {
      const previewElement = document.querySelector('.preview_animation');
      if (!document.fullscreenElement) {
        previewElement.requestFullscreen();
        fullScreen.textContent = 'Exit full screen';
      } else if (document.exitFullscreen) {
        document.exitFullscreen();
        fullScreen.textContent = 'Full screen';
      }
      // if (previewElement.requestFullscreen) {
      //   previewElement.requestFullscreen();
      // } else if (previewElement.msRequestFullscreen) {
      //   previewElement.msRequestFullscreen();
      // } else if (previewElement.mozRequestFullScreen) {
      //   previewElement.mozRequestFullScreen();
      // } else if (previewElement.webkitRequestFullscreen) {
      //   previewElement.webkitRequestFullscreen();
      // }
    });

    let flag = false;
    centerCanvas.addEventListener('mousedown', () => {
      flag = true;
    });
    centerCanvas.addEventListener('mouseup', () => {
      flag = false;
    });
    centerCanvas.addEventListener('mousemove', (event) => {
      if (flag) AppView.drawCenter(event);
    });
  }
}
