import FrameView from './frameview';

function drawCenterFromFrame() {
  const currentFrame = document.querySelector('.current canvas');
  const centerCanvas = document.querySelector('.center_canvas');
  centerCanvas.getContext('2d').clearRect(0, 0, centerCanvas.width, centerCanvas.height);
  centerCanvas.getContext('2d').drawImage(currentFrame, 0, 0, centerCanvas.width, centerCanvas.height);
}

const timeOuts = [];

export default class AppView {
  static renderInitialPage() {
    const header = document.createElement('header');
    header.innerHTML = `
    <div class="menu-first">
      <h1>CodeJam Animation-player</h1>
    </div>`;
    document.body.appendChild(header);
    const main = document.createElement('main');
    main.classList.add('main-container');
    main.innerHTML = `
    <aside class="frames-list">
      <button class="add_frame">+</button>
    </aside>
    <div>
      <canvas class="center_canvas" height="500px" width="500px"></canvas>
    </div>
    <div class="preview_animation">
      <canvas class="preview_canvas" height="200px" width="200px"></canvas>
      <div>
        <input type="range" name="speed" min="1" max="24" step="1" value="5"></input>
      </div>
      <button class="full_screen">Full screen</button>
    </div>`;
    document.body.appendChild(main);
    const newFrame = new FrameView();
    newFrame.renderFrame();
  }

  static addNewFrame() {
    const newFrame = new FrameView();
    const frameList = document.querySelector('.frames-list');
    frameList.querySelector('.current').classList.remove('current');
    newFrame.renderFrame();
    frameList.scrollTop = frameList.scrollHeight - frameList.clientHeight;
    drawCenterFromFrame();
  }

  static deleteFrame(frame) {
    const frameList = document.querySelector('.frames-list');
    if (frameList.childElementCount > 2) {
      let flag = false;
      if (frame.classList.contains('current')) flag = true;
      frameList.removeChild(frame);
      if (flag) frameList.firstElementChild.classList.add('current');
      drawCenterFromFrame();
    }
  }

  static copyFrame(frame) {
    const newFrame = new FrameView(frame.querySelector('canvas'));
    const frameList = document.querySelector('.frames-list');
    frameList.querySelector('.current').classList.remove('current');
    newFrame.renderFrame();
    frameList.scrollTop = frameList.scrollHeight - frameList.clientHeight;
    drawCenterFromFrame();
  }

  static changeCurrentFrame(frame) {
    if (!frame.classList.contains('current')) {
      const frameList = document.querySelector('.frames-list');
      frameList.querySelector('.current').classList.remove('current');
      frame.classList.add('current');
      drawCenterFromFrame();
    }
  }

  static animateFrame(fps) {
    if (timeOuts.length > 0) {
      timeOuts.forEach(element => clearTimeout(element));
    }
    const frameList = document.querySelector('.frames-list');
    const frames = [...frameList.children].slice(0, frameList.children.length - 1);
    const canvases = frames.map(element => element.querySelector('canvas'));
    const previewCanvas = document.querySelector('.preview_canvas');
    let i = 0;
    function drawPreviewFromFrame() {
      previewCanvas.getContext('2d').clearRect(0, 0, previewCanvas.width, previewCanvas.height);
      previewCanvas.getContext('2d').drawImage(canvases[i % canvases.length], 0, 0, previewCanvas.width, previewCanvas.height);
      i += 1;
      timeOuts.push(setTimeout(() => {
        requestAnimationFrame(drawPreviewFromFrame);
      }, 1000 / fps));
    }
    if (canvases.length > 1) drawPreviewFromFrame();
  }

  static drawCenter(event) {
    const centerCanvas = document.querySelector('.center_canvas');
    const x = event.pageX / 2;
    const y = event.pageY / 2;
    const radius = 10;
    const context = event.target.getContext('2d');
    context.beginPath();
    const radialGradient = context.createRadialGradient(x, y, 1, x, y, radius);
    radialGradient.addColorStop(0, 'black');
    context.fillStyle = radialGradient;
    context.arc(x, y, radius, 0, Math.PI * 2, false);
    context.fill();
    context.closePath();
    const currentFrame = document.querySelector('.current canvas');
    currentFrame.getContext('2d').drawImage(centerCanvas, 0, 0, currentFrame.width, currentFrame.height);
  }
}
